import React from "react";

export default (props) => {
    const headings = ["ACTION","ADDED","DUE","LATEST","LIMITATIONS","OWNER","RESPONSIBLE","STATUS","TYPE","id"];

    return(<div className="problem">
        {headings.map((item,i) => {
            const details = props.data[headings[i]];
            return(<div className="problem__box" key={i}>
                <div className={`problem__${item}`} data-heading>{item}</div>
                <div>{`${details}.`}</div>
            </div>)
        })}
    </div>);
}