import React, { Component } from "react";
import { Link } from "react-router-dom";
import sprite from "../../image/symbol-defs.svg";
import logo from "../../image/smallE.001.png";

export default class Sidebar extends Component {
    constructor() {
        super()
        this.state = { activeIndex: 0, }
    }
    render() {
        const locations = [{text: "home", icon: "icon-suitcase-with-white-details"}, {text: "dashboard", icon: "icon-bar-graph-on-a-rectangle"}, {text: "problem", icon: "icon-open-wrench-tool-silhouette"}, {text: "change", icon: "icon-forward-button"}]

        return(<div className="sidebar">
            <div className="header">
                <img src={logo} alt="logo" className="header__img" placeholder="Search"/>
                <div className="header__title">Report Deck</div>
            </div>
            {locations.map((item,i) => <Link to={`/${item.text}`} className={`sidebar__item ${i === this.state.activeIndex ? "sidebar__item-active" : null}`} onClick={() => this.setState({ activeIndex: i })} key={i}>
                <svg className="sidebar__icon">
                    <use xlinkHref={`${sprite}#${item.icon}`}/>
                </svg>
                <span className="sidebar__item-text">{item.text}</span>
            </Link>)}
        </div>);
    }
}