import React from "react";

export default (props) => {
    const headings = ["CLASS", "COORDINATOR", "END", "SERVICE", "START", "STATUS", "SUMMARY", "id"];

    return(<div className="change">
        {headings.map((item,i) => <div key={i} className="change__box">
            <div className={`change__${item}`} data-heading>{item}</div>
            <div>{`${props.data[item]}`}</div>
        </div>)}
    </div>);
}