import React, { Component } from "react";
import { Route, Switch } from "react-router-dom";
import Search from "./searchbox";
import Card from "./listcard";
import Dashboard from "./dashboard";
import Problems from "./problems";
import Changes from "./changes";

export default class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            times: props.dbIncidents.slice(0,6).map(item => parseInt(item.uptime - item.downtime)),
            applications: props.dbIncidents.slice(0,6).map(item => item.application),
            search: "",
        }
        this.searchEngine = this.searchEngine.bind(this);
    }
    searchEngine(event) {
        this.setState({search: event.target.value.substr(0,20)});
    }
    render() {
        let filteredIncidents = this.props.dbIncidents.filter(item => 
            item["impact"].toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1),
        filteredProblems = this.props.dbProblems.filter(item =>
            item["ACTION"].toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1),
        filteredChanges = this.props.dbChanges.filter(item => 
            item["SUMMARY"].toLowerCase().indexOf(this.state.search.toLowerCase()) !== -1);

        return(<div className="main">
            <Route render={({location}) => <Switch location={location}>
                {["/","/home"].map((item,i) => <Route key={i} path={item} exact render={() => 
                    <React.Fragment>
                        <Search search={this.searchEngine} list={filteredIncidents} category="impact" component={Card}/>
                        {filteredIncidents.map((item,i) => <Card data={item} key={i}/>)}
                    </React.Fragment>}
                />)}

                <Route path="/dashboard" render={() => 
                    <Dashboard times={this.state.times} applications={this.state.applications}/>
                }/>

                <Route path="/problem" render={() => 
                    <React.Fragment>
                        <Search list={filteredProblems} category="ACTION" component={Problems}/>
                        {filteredProblems.map((item,i) => <Problems data={item} key={i}/>)}
                    </React.Fragment>
                }/>

                <Route path="/change" render={() => 
                    <React.Fragment>
                        <Search list={filteredChanges} category="SUMMARY" component={Changes}/>
                        {filteredChanges.map((item,i) => <Changes data={item} key={i}/>)}
                    </React.Fragment>
                }/>
                
                <Route render={() => <div>Page Not Found</div>}/>
            </Switch>}/>
        </div>);
    }
}