import React, { Component } from "react";
import sprite from "../../image/symbol-defs.svg";

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            show_content: false,
        };
        this.selected = this.selected.bind(this);
        this.time = this.time.bind(this);
        this.started = this.started.bind(this);
    }
    selected() {
        this.setState({ show_content: !this.state.show_content });
    }
    time(downtime, uptime) {
        const duration = uptime - downtime;
        let minutes = parseInt((duration / (1000 * 60)) % 60), hours = parseInt((duration / (1000 * 60 * 60)) % 24);

        hours = (hours < 10) ? "0" + hours : hours;
        minutes = (minutes < 10) ? "0" + minutes : minutes;

        return `${hours}h ${minutes}m`;
    }
    started(downtime) {
        return `${new Date(downtime).toString().slice(0,21)}`;
    }
    render() {
        const { impact, description, action, status, downtime, uptime, priority, manager, reporter, resolver } = this.props.data;

        const classes = this.state.show_content ? {display: "block"} : {};
        const duration = this.time(downtime, uptime);
        
        return(<div className="list__card">
            <div className="list__card-title">
                {`${this.started(downtime)}`}
                <h4>{impact}</h4>
                <div className="list__card-extra">
                <span>Priority: </span>
                    <span>{priority}</span>
                </div>
            </div>
            <div className="list__card-extra">
                <svg className="list__card-icon">
                    <use xlinkHref={`${sprite}#icon-time`}/>
                </svg>
                <span>{duration}</span>
            </div>
            <div className="list__card-detail">{description}</div>
            <div className="list__card-extra">
                <svg className="list__card-icon">
                    <use xlinkHref={`${sprite}#icon-refresh-arrow`}/>
                </svg>
                <span>{status}</span>
            </div>
            <button className="list__card-action" onClick={this.selected}>
                <h2>More</h2>
                <div className="list__card-action-text" style={classes}>
                    {action ? action.map((item,i) => <p className="list__card-action-text--actions" key={i}>{item}</p>) : <p>No Actions</p>}
                    <p>MANAGER &mdash; {manager}</p>
                    <p>REPORTER &mdash; {reporter}</p>
                    <p>RESOLVER &mdash; {resolver}</p>
                </div>
            </button>
        </div>);
    }
}