import React, { Component } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import firebase from "firebase";
import "firebase/firestore";
import Sidebar from "./comp/_sidebar";
import Main from "./comp/_main";
import "./style/app.scss";

// Initialize Firebase
const config = {
  apiKey: "AIzaSyCPexi8M8FFjmqjtoBTp3HJJiHwyk03S5M",
  authDomain: "report-manager-e3067.firebaseapp.com",
  databaseURL: "https://report-manager-e3067.firebaseio.com",
  projectId: "report-manager-e3067",
  storageBucket: "report-manager-e3067.appspot.com",
  messagingSenderId: "957180191625"
};
firebase.initializeApp(config);
const db = firebase.firestore();

export default class App extends Component {
  constructor() {
    super();
    this.state = {};
  }
  componentWillMount() {
    const cloudStoreIncidents = db.collection("incidents");
    const cloudStoreProblems = db.collection("problems");
    const cloudStoreChanges = db.collection("changes");
    const orderIncidentsByDate = cloudStoreIncidents.orderBy("downtime", "desc");
    const orderProblemsByDate = cloudStoreProblems.orderBy("DUE", "desc");
    const orderChangesByDate = cloudStoreChanges.orderBy("START", "desc");

    orderIncidentsByDate.get().then((querySnapshot) => {
      let items = [];
      querySnapshot.forEach((doc) => {
        items.push({...doc.data(), id: doc.id});
      });
      this.setState({incidents: items});
    });
    orderProblemsByDate.get().then((querySnapshot) => {
      let items = [];
      querySnapshot.forEach((doc) => {
        items.push({...doc.data(), id: doc.id});
      });
      this.setState({problems: items});
    });
    orderChangesByDate.get().then((querySnapshot) => {
      let items = [];
      querySnapshot.forEach((doc) => {
        items.push({...doc.data(), id: doc.id});
      });
      this.setState({changes: items});
    });
  }
  render() {
    return(<Router>
      <div className="container">
        <Sidebar/>
        {this.state.incidents && this.state.problems && this.state.changes ? <Main dbIncidents={this.state.incidents} dbProblems={this.state.problems} dbChanges={this.state.changes}/> : <div className="loading">Page Loading...</div>}
      </div>
    </Router>);
  }
}