import React from "react";

export default (props) => {
    const highest = Math.max(...props.times.map((item) => item/60000/10));
    const hours = (ms) => {
        const hours = parseInt((ms / (1000 * 60 * 60)) % 24);
        return(hours < 10 ? "0" + hours : hours);
    };
    const minutes = (ms) => {
        const minutes = parseInt((ms / (1000 * 60)) % 60);
        return(minutes < 10 ? "0" + minutes : minutes);
    };

    return(<div className="charts">
        <div className="title">
            <h3>Latest Incident Infographic</h3>
            <h4>Ordered by date descending (earliest to oldest) </h4>
        </div>
        <div className="barchart">
            {props.applications.map((item,i) => {
                const width = ((props.times[i]/60000/10)*100) / highest;

                return <div className="barchart__items" key={i}>
                    <div className="barchart__title">{item[0]}</div>
                    <div className="barchart__bar" style={{width: `${width}%`}}>{`${hours(props.times[i])}h${minutes(props.times[i])}m`}</div>
                </div>
            })}
        </div>
    </div>);
}